.skills-container {
    margin: 4rem 0;
    position: relative;
    padding-top: 4rem;
}
.skills-container::after,
.skills-container::before {
    content: '';
    width: 28rem;
    height: 28rem;
    border-radius: 28.125rem;
    background: #7c66e3;
    position: absolute;
    z-index: -1;
    filter: blur(200px);
}
.skills-container::after {
    top: -3rem;
    left: -5rem;
}
.skills-container::before {
    background: #9e51b9;
    bottom: 0rem;
    right: 0rem;
}
.skills-container h5 {
    width: 100%;
    font-size: 100px;
    font-weight: 600;
    color: rgba(255, 255, 255, .1);
    text-align: center;
}
.skills-content {
    display: flex;
    align-items: center;
    gap: 3rem;
}
.skills {
    flex: 1;
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
}
.skills-info {
    flex: 1;
}
@media ( max-width: 1135px ) {
    .skills-container h5 {
        font-size: 80px;
    }
}
@media (max-width: 1025px) {
    .skills {
        padding-left: 1rem;
    }
    .skills,
    .skills-content {
        grid-gap: 2rem;
    }
    .skills-container h5 {
        font-size: 70px;
    }
}
@media ( max-width: 810px ) {
    .skills-container h5 {
        font-size: 60px;
    }
}
@media (max-width: 768px) {
    .skills-content {
        flex-direction: column-reverse;
        gap: 3rem;
    }
    .skills,
    .skills-info {
        width: 100%;
    }
    .skills-container h5 {
        font-size: 60px;
    }
    .skills-container::after,
    .skills-container::before {
        width: 18rem;
        height: 18rem;
    }
}
@media ( max-width: 700px ) {
    .skills-container h5 {
        font-size: 50px;
    }
}
@media (max-width: 600px) {
    .skills-container h5 {
        font-size: 40px;
    }
}
@media (max-width: 480px) {
    .skills-container h5 {
        font-size: 30px;
    }
}
@media (max-width: 375px) {
    .skills-container h5 {
        font-size: 25px;
    }
}
@media (max-width: 320px) {
    .skills-container h5 {
        font-size: 18px;
    }
}